import {FETCH_INCOME_SAGA} from '../actions/incomeActions'

const incomeData = (state = {
    flights:{}
}, action) => {
    switch (action.type) {
        case FETCH_INCOME_SAGA:
            const   {data}  = action.payload
            //console.log('reducer saga',action.payload)
            return { ...state,data}
        default:
            return state
    }
}

export default incomeData;