import React from 'react'
import { Switch, Route } from 'react-router-dom';

import Home from './components/Home'
import HomeIncome from "./components/incomes/HomeIncome";
import Login from "./components/auth/Login";
import RFCform from "./components/forms/RFCform";
import InfoRfc from "./components/info/InfoRFC"
import InfoSat from "./components/info/InfoSat";
const Routes = () => (

    <Switch>
        <Route exact path='/' component={Home} />
        <Route exact path='/login' component={Login} />
        <Route exact path='/income' component={HomeIncome} />
        <Route exact path='/rfcForm' component={RFCform} />
        <Route exact path='/info' component={InfoRfc} />
        <Route exact path='/goSat' component={InfoSat} />
    </Switch>
);

export default Routes;