const columns = [{
    title: 'Producto/Servicio',
    dataIndex: 'name',
    key: 'name',
    render: text => <a href="javascript:;">{text}</a>,
}, {
    title: 'Descripcion',
    dataIndex: 'description',
    key: 'description',
}, {
    title: 'Costo',
    dataIndex: 'amount',
    key: 'amount',
}];


class ListIncome extends Component {
    state ={
        incomes:[],
        income:{
            amount:0,
            iva:0
        }
    }

    componentDidMount() {

    }
    onChange = (e) =>{
        const {income} = this.state
        income[e.target.name] = e.target.value
        this.setState({income})
    }
    handleIva = (e) => {
        console.log('iba')
        const {income} = this.state
        const field = e.target.name
        income[field] = e.target.value
        console.log(income.amount)
        let iva = parseInt(income.amount)* 0.15; //esto da not a number
        income.iva = iva
        this.setState({income})
    }
    addIncome = (e) => {
        e.preventDefault()
        const {incomes,income} = this.state
        incomes.push(income)
        this.setState({incomes})
        this.setState({income:{
                amount:0,
                iva:0
            }})
        console.log('se agrego:',this.state)
    }
    render() {
        return (
                <div style={{marginTop: "1rem",marginRight:"2rem",marginLeft:"2rem"}}>
                    <CreateIncome addIncome={this.addIncome} onChange={this.onChange} handleIva={this.handleIva}{...this.state.income} />
                    <Table columns={columns} dataSource={this.state.incomes} />
                </div>
        )
    }
}


