import { FETCH_LOGIN_SAGA} from "../actions/authActions";
import {FETCH_FORM_SAGA} from "../actions/formActions";

const formData = (state = {
    user:{}
}, action) => {
    switch (action.type) {
        //set the data from saga to the state
        case FETCH_FORM_SAGA:
            console.log('payload login:',action.payload)//undefined
            const  user = action.payload
            return { ...state,user}
        //case CLEAN_USER:

        default:
            return state
    }
}

export default formData;
