//Action types

export const CREATE_FORM_SAGA = 'CREATE_FORM_SAGA'
export const FETCH_FORM_SAGA = 'FETCH_FORM_SAGA'
//Action Creators


export const createFormSaga = (payload) => ({
    type : CREATE_FORM_SAGA,
    payload
})

export const fetchFormSaga = (payload) => ({
    type: FETCH_FORM_SAGA,
    payload
})
