//Action Types
export const FETCH_INCOME_SAGA = 'FETCH_INCOME_SAGA'
export const GET_INCOME_SAGA = 'GET_INCOME_SAGA'
export const CREATE_INCOME_SAGA = 'GET_INCOME_SAGA'

//Action Creators

export const createIncomeSaga = (payload) => ({
    type : CREATE_INCOME_SAGA,
    payload
})

export const getIncomeSaga = (payload) => ({
    type : GET_INCOME_SAGA,
    payload
})

export const fetchIncomeSaga = (payload) => ({
    type : FETCH_INCOME_SAGA,
    payload
})