import { call, put, takeEvery, all } from 'redux-saga/effects';

import { login, signup } from "../middleware/auth"
import {fetchLoginSaga, GET_LOGIN_SAGA} from "../actions/authActions";
import {fetchFormSaga, CREATE_FORM_SAGA} from "../actions/formActions"

//----------------------------------------------------
//--------------Auth Functions -----------------------
//----------------------------------------------------
function* loginSaga(payload) {
    try{
        const {data} = yield call(login,payload)
        yield put(fetchLoginSaga(data));

    } catch (e) {
        console.log(e)
    }
}

function* signupSaga(info) {
    try{
        //const {data} = yield call(signup);
        const {data} = yield call(signup,info.payload)
        console.log('data after last action',data)
        yield put (fetchFormSaga(data))
    } catch (e) {
        console.log(e)
    }
}


//----------------------------------------------------
//--------------Get FORMS Data-----------------------
