import React,{Component} from 'react'
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {createFormSaga} from '../../actions/formActions'
import {
    Form, Input, Icon, Button, Select,Collapse
} from 'antd';

const Option = Select.Option;
const Panel = Collapse.Panel;



class RFCform extends Component{
    state = {
        user:{
            nomina:'true',
            impuestos:'true',
            rfc:'true',
            CIECF:'true',
            FIEL:'true',
            enfoque:'true',
            producto:'true',
            regimen:'true'
        }
    }

    handleChange = (e) => {
        const {user} = this.state
        user[e.target.name]=e.target.value
        this.setState({user})
    }

    handleNomina = (e)=>{
        console.log(e)
        const {user} = this.state
        user['nomina']=e
        this.setState({user})
    }
    handleImpuestos = (e)=>{
        console.log(e)
        const {user} = this.state
        user['impuestos']=e
        this.setState({user})
    }
    handleRfc = (e)=>{
        console.log(e)
        const {user} = this.state
        user['rfc']=e
        this.setState({user})
    }
    handleAdvance = (e)=>{
       if(e == 'false'){
           this.props.history.push('/goSat');
       }
    }
    handleCIECF = (e)=>{
        console.log(e)
        const {user} = this.state
        user['CIECF']=e
        this.setState({user})
    }
    handleFIEL = (e)=>{
        console.log(e)
        const {user} = this.state
        user['FIEL']=e
        this.setState({user})
    }
    handleEnfoque = (e)=>{
        console.log(e)
        const {user} = this.state
        user['enfoque']=e
        this.setState({user})
    }
    handleProducto = (e)=>{
        console.log(e)
        const {user} = this.state
        user['producto']=e
        this.setState({user})
    }
    handleRegimen = (e)=>{
        console.log(e)
        const {user} = this.state
        user['regimen']=e
        this.setState({user})
    }
    signUp = () =>{
        console.log('logIn')
        const {user} = this.state
        console.log(user)
        this.props.createFormSaga(user)
        //this.props.getLoginSaga(user)
    }

    render() {
        const { email,password } = this.state.user;
        //logged function
        if(this.props.formData.user._id){
            console.log('loged')
            this.props.history.push('/info');
        }
        return(
            <div className="divChildSpace" style={{margin:"2rem"}}>
            <Form >

                <div>¿Conoces tu CIECF (Contraseña otorgada por el SAT al momento de darte de alta ante el RFC)?<br/>
                    o tu Firma Electrónica Avanzada (FIEL o e-firma)? (Esta incluye dos archivos electrónicos .cer y .key más tu contraseña)
                    <Select name="
                type" defaultValue="true" onChange={this.handleAdvance}>
                        <Option value="true">Si</Option>
                        <Option value="false">No</Option>
                    </Select>
                </div>
                <Collapse >
                    <Panel header="Click para continuar Formulario" key="1">
                        <div>Nombre :
                            <Input
                                style={{marginBottom:'1rem'}}
                                name='name'
                                placeholder="Nombre Completo"
                                onChange={this.handleChange}
                            />
                        </div>
                        <div>Correo Electronico:
                            <Input
                                style={{marginBottom:'1rem'}}
                                name='email'
                                placeholder="Correo"
                                onChange={this.handleChange}
                            />
                        </div>
                        <div>
                            <Input.Password
                                style={{marginBottom:'1rem'}}
                                name='password'
                                placeholder="Password"
                                onChange={this.handleChange}
                            />
                        </div>
                        <div>¿Recibes ingresos vía nómina?
                            <Select name="
                type" defaultValue="true" onChange={this.handleNomina}>
                                <Option value="true">Si</Option>
                                <Option value="false">No</Option>
                            </Select>
                        </div>
                        <div>¿La empresa para la que trabajas te retiene impuestos?
                            <Select name="
                type" defaultValue="true" onChange={this.handleImpuestos}>
                                <Option value="true">Si</Option>
                                <Option value="false">No</Option>
                            </Select>
                        </div>
                        <div>¿Conoces tu RFC?
                            <Select name="
                type" defaultValue="true" onChange={this.handleRfc}>
                                <Option value="true">Si</Option>
                                <Option value="false">No</Option>
                            </Select>
                        </div>
                        <div>¿Tipo de negocio al que te enfocas?
                            <Select name="
                type" defaultValue="Comercio" onChange={this.handleEnfoque}>
                                <Option value="Comercio">Comercio</Option>
                                <Option value="Producto">Producto</Option>
                                <Option value="Servicios">Servicios</Option>
                            </Select>
                        </div>
                        <div>Producto o servicio que venderás / otorgarás
                            <Select name="
                type" defaultValue="Venta de lentes y accesorios" onChange={this.handleProducto}>
                                <Option value="Venta de lentes y accesorios">Venta de lentes y accesorios</Option>

                            </Select>
                        </div>
                        <div>¿Has tributado en algún otro régimen?
                            <Select name="
                type" defaultValue="true" onChange={this.handle}>
                                <Option value="true">Si</Option>
                                <Option value="false">No</Option>
                            </Select>
                        </div>
                        <div>¿Recibes ingresos vía nómina?
                            <Select name="
                type" defaultValue="true" onChange={this.handleRegimen}>
                                <Option value="true">Si</Option>
                                <Option value="false">No</Option>
                            </Select>
                        </div>

                    </Panel>
                </Collapse>


                <div>
                    <Button type="primary" onClick={this.signUp} block>Obtener Solucion</Button>
                </div>
            </Form>
            </div>
        )
    }

}

//Set the main stage to props i need to use on this component
const mapStateToProps = (state) => {
    const {
        formData
    } = state;

    return {
        formData
    };
};


//Set the acction is going to be trigerred on this component
const mapDispatchToProps = (dispatch) => {

    return bindActionCreators({
        createFormSaga
    }, dispatch);

};

export default connect(mapStateToProps, mapDispatchToProps)(RFCform);
